import Axios from "axios";
import { USER_DETAILS_FAIL, USER_DETAILS_REQUEST, USER_DETAILS_SUCCESS, USER_REGISTER_FAIL, USER_REGISTER_REQUEST, USER_REGISTER_SUCCESS, USER_SIGNIN_FAIL, USER_SIGNIN_REQUEST, USER_SIGNIN_SUCCESS, USER_SIGNOUT, USER_UPDATE_PROFILE_FAIL, USER_UPDATE_PROFILE_REQUEST, USER_UPDATE_PROFILE_SUCCESS } from "../constants/usersConstants"

export const registrar = (name, email, password) => async(dispatch)=>{
    dispatch({type: USER_REGISTER_REQUEST, payload:{email, password}});
    try {
        const { data } = await Axios.post('/api/usuarios/registrar', {name, email, password});
        dispatch({type: USER_REGISTER_SUCCESS, payload: data});
        dispatch({type: USER_SIGNIN_SUCCESS, payload: data});
        localStorage.setItem('userInfo', JSON.stringify(data));
    } catch (error) {
        dispatch({
            type: USER_REGISTER_FAIL,
            payload: error.response && error.response.data.message ?
            error.response.data.message
            : error.message,
            
        });
    }
};




export const ingresar = (email, password) => async(dispatch)=>{
    dispatch({type: USER_SIGNIN_REQUEST, payload:{email, password}});
    try {
        const { data } = await Axios.post('/api/usuarios/ingresar', {email, password});
        dispatch({type: USER_SIGNIN_SUCCESS, payload: data});
        localStorage.setItem('userInfo', JSON.stringify(data));
    } catch (error) {
        dispatch({
            type: USER_SIGNIN_FAIL,
            payload: error.response && error.response.data.message ?
            error.response.data.message
            : error.message,
            
        });
    }
};
export const salir = () =>(dispatch)=>{
    localStorage.removeItem('userInfo');
    localStorage.removeItem('cartItems');
    localStorage.removeItem('shippingAddress');
    dispatch({type: USER_SIGNOUT});
};

export const detailsUser = (userId) => async (dispatch, getState) => {
    dispatch({ type: USER_DETAILS_REQUEST, payload: userId });
    const {
      userSignin: { userInfo },
    } = getState();
    try {
      const { data } = await Axios.get(`/api/usuarios/${userId}`, {
        headers: { Authorization: `Bearer ${userInfo?.token}` },
      });
      dispatch({ type: USER_DETAILS_SUCCESS, payload: data });
    } catch (error) {
      const message =
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message;
      dispatch({ type: USER_DETAILS_FAIL, payload: message });
    }
  };

  export const updateUserProfile = (usuario) => async (dispatch, getState) => {
    dispatch({type: USER_UPDATE_PROFILE_REQUEST, payload: usuario});
    const{
      userSignin: { userInfo },
    } = getState();
    try {
      const { data } = await Axios.put(`/api/usuarios/profile`, usuario,{
        headers: { Authorization: `bearer ${userInfo.token}` },
      });
      dispatch({type: USER_UPDATE_PROFILE_SUCCESS, payload: data});
      dispatch({type: USER_SIGNIN_SUCCESS, payload: data});
      localStorage.setItem('userInfo', JSON.stringify(data));
    } catch (error) {
      const message =
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message;
      dispatch({type: USER_UPDATE_PROFILE_FAIL, payload: message});
    }
  }